.main-btn{
    @include layout(160px, 60px);
    background-color: $primary;
    border-radius: 10px;
    border: 0;
    @include font($ns, 16px, #fff);
    transition: all 0.3s;
    cursor: pointer;

    &:hover{
        background-color: $primary-80;
        color: $primary;
        transform: scale(1.1);
    }
}