.sign-up--wrap{
    @include flex(flex-start, center);

    .sign-up--form{
        @include flex(center, center);
        @include layout(50%, calc(100vh - 80px));
        background-color: $primary-20;

        .sign-up-box{
            @include flex(flex-start, flex-start, column);
            @include layout(600px, 650px);
            padding: 50px;
            background-color: #fff;
            border-radius: 20px;
            box-shadow: 0px 0px 20px 0px #d9d9d9;

            .sign-up-title{
                margin-bottom: 70px;
                @include font($ns, 40px);
            }
            .sign-up-input{
                width: 100%;

                .user-input{
                    margin-bottom: 30px;
                }
            }
            .main-btn{
                margin: auto;
                font-size: 18px;
                text-align: center;
                line-height: 60px;
            }
        }
    }
    .sign-up-img{
        @include layout(50%, calc(100vh - 80px));
    }
}