.result-wrap{
    @include flex(center, flex-start, column);
    @include layout(100%, calc(100vh - 80px));
    background-color: $primary-20;
    
    .worldcup-result{
        @include layout(100%, auto);
        padding: 30px 0;
        background-color: $primary-50;
        @include font($ns, 24px);
        text-align: center;

        span{
            color: $primary;
            font-weight: 700;
        }
    }
    .result-content{
        @include layout(50%, 50%);
        margin-top: 130px;
    }
    .main-btn{margin-top: 80px;}
}