.user-input{
    @include flex(flex-start, center, column);
    margin-bottom: 40px;

    label{
        margin-bottom: 18px;
        @include font($ns, 24px, #000);
    }
    input{
        @include layout(100%, 45px);
        padding: 10px;
        @include border(1px, $primary-60, 6px);
        @include font($ns, 14px, $primary);

        &::placeholder{
            @include font($ns, 14px, #959595);
        }
    }
}