.login-wrap{
    @include layout(100%, 100%);
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.20);
    transition: all 0.2s;

    .inner{
        position: relative;
        @include layout(1500px, 100%);

        .login-box{
            @include flex(center, flex-start, column);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            @include layout(600px, 750px);
            padding: 50px;
            background-color: #fff;
            @include border(1px, #efefef, 12px);
            box-shadow: 0px 0px 30px 0px #e9e9e9;

            .close-btn{
                width: 100%;
                text-align: right;
                cursor: pointer;
                user-select: none;
            }
            .login-img{
                width: 180px;
                filter: drop-shadow(2px 4px 6px #c1c1c1);
            }
            .login-title{
                @include font($ns, 40px, $primary);
            }
            .login-input--wrap{
                width: 100%;
                margin-top: 60px;

                .login-pw{
                    margin-bottom: 20px;
                }
                .sign-up{
                    margin-bottom: 24px;
                    @include font($ns, 16px, #a5a5a5);
                    text-align: right;
                    transition: all 0.2s;
                    cursor: pointer;
                    animation: upDown 1s infinite;

                    &:hover{
                        color: #3d3d3d;
                    }
                }
                .main-btn{
                    margin-top: 35px;
                    font-size: 20px;
                }
            }
        }
    }
}

@keyframes upDown {
    0%{transform: translateY(0px); color: $primary;}
    50%{transform: translateY(5px); color: #a5a5a5;}
    100%{transform: translateY(0px); color: $primary;}
}