.recommend-wrap{
    .recommend-title{
        @include layout(100%, auto);
        padding: 30px 0;
        background-color: $primary-50;
        @include font($ns, 24px);
        text-align: center;

        span{
            font-weight: 700;
            animation: light 1s infinite;
            &:nth-child(2){animation-delay: 0.5s;}
            &:last-child{
                animation: none;
                color: $red;
            }
        }
    }
    .recommend-content--airplane{
        margin-bottom: 70px;

        .time-table{
            @include flex(center, flex-start);
            gap: 32px;
            
            .air-way{
                @include flex(center, flex-start);
                gap: 40px;
                @include layout(auto, 150px);
                @include font($ns, 40px);
                text-align: center;

                .en-city{
                    margin-top: 10px;
                    @include font($ns, 20px, #aaa);
                }
            }
            .air-info{
                @include flex(center, flex-start);
                @include font($ns, 24px, $primary);

                .air-time{
                    margin-right: 24px;
                    color: #78b3ff;
                }
                .air-date{
                    text-decoration: underline;
                }
                .air-person{
                    margin-right: 16px;
                }
                .emoji{
                    margin-right: 8px;
                }
                .ticket-btn--wrap{
                    @include flex(center, flex-start);
                    gap: 16px;

                    .ticket-btn{
                        @include flex(center, center);
                        padding: 8px 10px;
                        @include font($ns, 12px, #fff);
                        @include border(1px, #fff, 8px);
                        transition: all 0.3s;
                        cursor: pointer;

                        &:hover{
                            box-shadow: 0px 0px 12px 5px #e1e1e1;
                        }

                    }
                    .add-btn{
                        background-color: $red;
                    }
                    .remove-btn{
                        background-color: $primary;
                    }
                }
            }
        }
        .ticket-info{
            @include font($ns, 24px);
            thead{
                tr{
                    height: 40px;
                    background-color: $primary;
                    
                    
                    th{
                        line-height: 35px;
                        color: #fff;

                        &:first-child{border-top-left-radius: 8px;}
                        &:last-child{border-top-right-radius: 8px;}
                    }
                }
            }
            tbody{
                tr{
                    height: 60px;
                    background-color: $primary-20;

                    td{
                        padding-top: 16px;
                        text-align: center;

                        &:first-child{border-bottom-left-radius: 8px;}
                        &:last-child{border-bottom-right-radius: 8px;}
                    }
                    .air-company{
                        font-weight: 600;
                    }
                    .ticket-price{
                        font-weight: 600;
                        color: $red;
                    }
                }
            }
        }
    }
}

.btn-wrap{
    display: flex;
    width: 100%;

    .main-btn{
        margin-top: 0;
        margin: auto;
    }
}

@keyframes light{
    0%{color: #fff;}
    50%{color: $red;}
    100%{color: #fff;}
}